header {
    background: var(--azul);
    border-bottom: var(--borda) var(--amarelo);
}

header div.menu {
    height: auto;
    padding: 7.5px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

header nav {
    display: none;
    position: absolute;
    top: 60.3px;
    transition-duration: 1s;
    right: calc(-100vw);
}

.menu-toggle div {
    height: 3px;
    width: 20px;
    background: var(--branco);
    margin: 2px 0;
    border-radius: 25px;
}

header .menu.on nav {
    display: block;
    display: flex;
    flex-direction: column;
    transform: translateX(-100vw);
    transition-duration: 1s;
    height: auto;
    width: 100vw;
    background: var(--preto);
}

header .menu.on nav li {
    padding: 10px 25px;
    text-align: center
}

header .menu.on .two {
    opacity: 0;
}

header .menu.on .one {
    transform: rotate(45deg) translate(3px, 3px)
}

header .menu.on .three {
    transform: rotate(-45deg) translate(4px, -4px)
}

header .menu .one, .two, .three {
    transition-duration: 0.3s;
}

header nav div {
    border-top: var(--borda) #444;
    width: 65%;
    margin: 15px auto;
}

header nav div i {
    font-size: 30px;
    color: #43E460;
}